export default {
    name: "goldgood",
    data(){
        return{
            gid:"",
            good:[]
        }
    },
    mounted:function () {
        this.gid=this.$route.query.id;
        this.$axios.get('/user/exchange_goods_info?goods_id=45').then(res=>{
                this.good=res.data.data;
        })
    }
}



